// src/app/dashboard/page.tsx
"use client";

import React from "react";
import { useAttendanceStore } from "@/store/attendanceStore";
import UploadAttendance from "@/components/UploadAttendance";
import Card from "@/components/Card";
import AttendanceTable from "@/components/AttendanceTable";

export default function DashboardPage() {
  const records = useAttendanceStore((s) => s.records);

  const totalEmployees = records.length;
  const presentToday = records.filter((r) => Number(r.presentDays ?? 0) > 0).length;
  const onBreak = records.filter((r) => (r.shift ?? "").toString().toLowerCase().includes("break")).length;
  const pendingApprovals = 0; // connect to approvals store later

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <div>
          <h1 className="text-2xl font-bold">Dashboard Overview</h1>
          <p className="text-sm text-muted">Quick summary of today's activity</p>
        </div>

        <UploadAttendance />
      </div>

      <div className="grid grid-cols-4 gap-4 mb-6">
        <Card title="Total Employees" value={totalEmployees} />
        <Card title="Present Today" value={presentToday} />
        <Card title="On Break" value={onBreak} />
        <Card title="Pending Approvals" value={pendingApprovals} />
      </div>

      <section>
        <h2 className="text-lg font-semibold mb-3">Attendance Summary</h2>
        <div className="text-sm text-muted mb-4">More sections can go here (employees table, shifts, logs, etc.)</div>

        <AttendanceTable />
      </section>
    </div>
  );
}
